import React from 'react'
import {Input, Stack} from '@chakra-ui/react'
import FormField from './FormField'
import useStore from '../hooks/useStore'
import {observer} from 'mobx-react'
import DrawerForm from './DrawerForm'
import useQueryValue from '../hooks/useQueryValue'
import {useHistory} from 'react-router-dom'
import Project from '../domain/Project'
import DateInput from '../ui/DateInput'
import {toDate} from '../util/date'

interface ProjectFormDrawerProps {
  name: string
}

const ProjectFormDrawer: React.FC<ProjectFormDrawerProps> = ({ name }) => {
  const history = useHistory()
  const {projectStore, organizationStore} = useStore()
  const projectId = useQueryValue(name)

  const handleSubmit = async ({dateStart, dateEnd, ...values}: any, projectId: string) => {
    values.dateStart = dateStart ? toDate(dateStart) : null
    values.dateEnd = dateEnd ? toDate(dateEnd) : null

    const project = (new Project()).fromObject(values)
    project.organizationId = organizationStore.currentOrganizationId

    if (projectId) {
      project.id = projectId
      await projectStore.update(project)
    } else {
      await projectStore.create(project)
    }

    if (!project) {
      throw new Error('Something went wrong')
    }

    const query = `?highlighted-project-id=${project.id}`

    if (project.isOngoing()) {
      history.push(`/my-projects/active-projects${query}`)
    } else if (!project.hasStarted()) {
      history.push(`/my-projects/upcoming-projects${query}`)
    } else {
      history.push(`/my-projects/ended-projects${query}`)
    }
  }

  const project = {
    ...projectId ? projectStore.getById(projectId) : {}
  }

  return (
      <DrawerForm
          name={name}
          onSubmit={handleSubmit}
          header={<>{projectId ? 'Edit' : 'Add'} project</>}
          defaultValues={project}
          autoClose={false}
      >
        <Stack spacing={4}>
          <FormField
              name="name"
              required
              label="Project name"
          >
            <Input
                size="lg"
                autoFocus
            />
          </FormField>

          <FormField
              name="clientName"
              label="Client"
          >
            <Input />
          </FormField>

          <FormField
              name="dateStart"
              label="Starts at"
          >
            <DateInput />
          </FormField>

          <FormField
              name="dateEnd"
              label="Ends at"
          >
            <DateInput />
          </FormField>
        </Stack>
      </DrawerForm>
  )
}

export default observer(ProjectFormDrawer)