import React from 'react'
import { Input } from '@chakra-ui/react'

interface Column<T> {
  accessor: (item: T) => string | number | boolean | Date | T[keyof T] | T
  searchable?: boolean
}

export interface TableSearchProps<T> {
  onFilter: (callback: (row: T) => boolean) => void
  columns: Array<Column<T>>
  placeholder?: string
}

const TableSearch = <T extends object>({ columns, onFilter, placeholder }: TableSearchProps<T>) => {
  const handleFilter = (term: string) => {
    onFilter((row: T) => {
      if (!term) {
        return true
      }

      for (const column of columns) {
        if (column.searchable === false) {
          continue
        }

        const value = column.accessor(row)

        if (
            value
            && ['number', 'string'].includes(typeof value)
            && (value as any).toString().toLocaleLowerCase().includes(term.toLocaleLowerCase())
        ) {
          return true
        }
      }

      return false
    })
  }

  return (
      <Input
          placeholder={placeholder || 'Search..'}
          onChange={e => {
            handleFilter(e.target.value)
          }}
      />
  )
}

export default TableSearch