import React, {useState} from 'react'
import {Table, Thead, Tbody, Td, Tr, Th, Flex} from '@chakra-ui/react'
import {FaArrowDown, FaArrowUp} from 'react-icons/fa'
import {useHistory} from 'react-router-dom'
import orderBy from 'lodash/orderBy'
import TableSearch, {TableSearchProps} from './TableSearch'

export interface DataTableColumn<T> {
  accessor: (item: T) => string | number | boolean | Date | T[keyof T] | T
  render?: (value: string | number | boolean | Date | T[keyof T] | T, item: T, rowIndex: number) => any
  title?: string
  searchable?: boolean
  sortable?: boolean
  disableLink?: boolean
}

interface DataTableProps<T> {
  data: Array<T>
  columns: Array<DataTableColumn<T>>
  linkTo?: (item: T) => string
  search?: boolean | Omit<TableSearchProps<T>, 'columns' | 'onFilter'>
  sortable?: boolean
}

const DataTable = <T extends object>({
                                       data,
                                       columns,
                                       linkTo,
                                       search,
                                       sortable,
                                     }: DataTableProps<T>) => {
  const history = useHistory()
  const [[filter], setFilter] = useState<[(row: T) => boolean]>([() => true])
  const [sortBy, setSortBy] = useState<{ index: number; order: 'asc' | 'desc' } | null>(null)

  return (
      <div>
        {search && (
            <div>
              <TableSearch<T> onFilter={(newFilter: any) => setFilter([newFilter])} columns={columns} {...search} />
            </div>
        )}
        <Table variant="simple">
          <Thead>
            <Tr>
              {columns.map((column, index) => (
                  <Th
                      key={index}
                      onClick={
                        (column.sortable === undefined ? sortable : column.sortable)
                            ? (() => setSortBy({
                              index,
                              order: sortBy?.index === index && sortBy?.order === 'asc' ? 'desc' : 'asc'
                            }))
                            : undefined
                      }
                  >
                    <Flex alignItems="center">
                      {column.title}
                      &nbsp;
                      {sortBy && sortBy.index === index && (sortBy?.order === 'desc' ? <FaArrowDown/> : <FaArrowUp/>)}
                    </Flex>
                  </Th>
              ))}
            </Tr>
          </Thead>
          <Tbody>
            {(sortBy ? (orderBy([...data], columns[sortBy.index].accessor, sortBy.order) as T[]) : data || []).map(
                (item, rowIndex) => {
                  if (!filter(item)) {
                    return null
                  }

                  return (
                      <Tr
                          isLink={!!linkTo}
                          key={({...item} as { id: number | string }).id || rowIndex}
                          onClick={() => linkTo && history.push(linkTo(item))}
                      >
                        {columns.map((column, columnIndex) => {
                          const value = column.accessor(item)

                          return (
                              <Td
                                  onClick={e => column.disableLink && e.stopPropagation()}
                                  key={columnIndex}
                                  style={column.disableLink ? {cursor: 'default'} : {}}
                              >
                                {column.render ? column.render(value, item, rowIndex) : value}
                              </Td>
                          )
                        })}
                      </Tr>
                  )
                },
            )}
          </Tbody>
        </Table>
      </div>
  )
}

DataTable.defaultProps = {
  sortable: true,
}

export default DataTable