import React, {useEffect} from 'react'
import {useForm, UseFormMethods, FormProvider} from 'react-hook-form'
import {useToast} from '@chakra-ui/react'

export interface FormProps {
  onSubmit: (values: object) => void | Promise<void>
  children: React.ReactNode | ((formContext: UseFormMethods) => any)
  defaultValues?: object
  successMessage?: string
}

const Form: React.FC<FormProps> = ({onSubmit, children, defaultValues, successMessage}) => {
  const formContext = useForm({
    defaultValues
  })
  const {handleSubmit, reset, formState: {isSubmitSuccessful}} = formContext
  const toast = useToast()


  useEffect(() => {
    if (isSubmitSuccessful && successMessage) {
      toast({
        description: successMessage,
        status: 'success',
        isClosable: true
      })
    }
  }, [isSubmitSuccessful])

  useEffect(() => {
    if (isSubmitSuccessful) {
      reset(defaultValues)
    }
  }, [reset, defaultValues, isSubmitSuccessful])

  return (
      <FormProvider {...formContext}>
        <form onSubmit={handleSubmit(onSubmit)}>
          {typeof children === 'function' ? (children as any)(formContext) : children}
        </form>
      </FormProvider>
  )
}

export default Form