import React from 'react'
import Person from '../domain/Person'
import {observer} from 'mobx-react'
import User, {UserProps} from '../ui/User'

interface PersonUserProps extends Omit<UserProps, 'name' | 'subTitle' | 'image'> {
  person: Person
}

const PersonUser: React.FC<PersonUserProps> = ({person, ...props}) => {
  if (!person) {
    return null
  }

  return (
      <User name={person.name} subTitle={person.subTitle} image={person.image} {...props}/>
  )
}

export default observer(PersonUser)