import React from 'react'
import Project from '../domain/Project'
import {Button, HStack, VStack, Stack} from '@chakra-ui/react'
import ProjectCard from './ProjectCard'
import {Link} from 'react-router-dom'
import ProjectFormDrawer from './ProjectFormDrawer'
import useQueryValue from '../hooks/useQueryValue'

export interface ProjectListProps {
  projects: Project[]
}

const ProjectList: React.FC<ProjectListProps> = ({projects}) => {
  const highlightedProjectId = useQueryValue('highlighted-project-id')

  return (
      <div>
        <ProjectFormDrawer name="project" />
        {projects.length ? (
            <Stack spacing={4}>
              <HStack justifyContent="flex-end">
                <Button as={Link} to="?project">
                  Add project
                </Button>
              </HStack>
              <Stack>
                {projects.map(project => (
                    <ProjectCard highlighted={highlightedProjectId === project.id} key={project.id} project={project}/>
                ))}
              </Stack>
            </Stack>
        ) : (
            <VStack height="100%" justifyContent="center" spacing={8}>
              <div>No projects matching filters.</div>
              <Button colorScheme="purple" as={Link} to="?project">
                Create new project
              </Button>
            </VStack>
        )}
      </div>
  )
}

export default ProjectList