import React from 'react'
import {Input, ScaleFade, VStack} from '@chakra-ui/react'
import Logo from '../ui/Logo'
import FormSubmitButton from './FormSubmitButton'
import Form from './Form'
import FormField from './FormField'
import Person from '../domain/Person'

export interface OrganizationCreateFormProps {
  onSubmit: (data: any) => void
  currentPerson?: Person
}

const OrganizationCreateForm: React.FC<OrganizationCreateFormProps> = ({onSubmit, currentPerson}) => {
  return (
      <Form onSubmit={onSubmit}>
        {({watch}) => {
          const organizationName = watch('organization.name')

          return (
              <VStack spacing={8} mt={4}>
                <div style={{width: 150}}>
                  <Logo/>
                </div>
                <FormField name="organization.name" required>
                  <Input
                      size="lg"
                      variant="flushed"
                      placeholder="Organization name"
                      autoFocus
                  />
                </FormField>

                {organizationName && (
                    <ScaleFade in style={{width: '100%'}}>
                      <FormField
                          name="person.name"
                          required
                          defaultValue={currentPerson?.name || ''}
                      >
                        <Input
                            name="person.name"
                            variant="flushed"
                            placeholder="Your name"
                        />
                      </FormField>
                    </ScaleFade>
                )}

                {watch('person.name') && (
                    <ScaleFade in style={{width: '100%'}}>
                      <FormField name="person.position">
                        <Input
                            variant="flushed"
                            placeholder={'Your position in ' + organizationName}
                        />
                      </FormField>
                    </ScaleFade>
                )}

                <FormSubmitButton>
                  Create
                </FormSubmitButton>
              </VStack>
          )
        }}
      </Form>
  )
}

export default OrganizationCreateForm