import React, {useEffect} from 'react'
import {Avatar, FormLabel, Input} from '@chakra-ui/react'
import FormField from './FormField'
import useServices from '../hooks/useServices'
import {useFormContext} from 'react-hook-form'

interface FormImageFieldProps {
  label?: string
  name: string
}

const FormImageField: React.FC<FormImageFieldProps> = ({label, children, name}) => {
  const {attachmentUploadService} = useServices()
  const { setValue, watch } = useFormContext()

  useEffect(() => {
  }, [attachmentUploadService])

  return (
      <div>
        {label && (
            <FormLabel>
              {label}
            </FormLabel>
        )}
        <label style={{cursor: 'pointer'}}>
          <FormField name={name} />
          <Avatar src={watch(name)} size="xl" />
          <Input type="file" accept="image/*" style={{display: 'none'}} onChange={async e => {
            const file = e.target.files[0]

            if (!file) {
              return
            }

            const url = await attachmentUploadService.upload(file)

            setValue(name, url, { shouldDirty: true })
          }}/>
        </label>
      </div>
  )
}

export default FormImageField