import React from 'react'
import {Container, Button, VStack, Text} from '@chakra-ui/react'
import Logo from '../ui/Logo'
import {Link} from 'react-router-dom'

const Home: React.FC = () => {
  const buildInfo = process.env.REACT_APP_BUILD_DATE

  return (
      <Container>
        <VStack mt={4} spacing={10}>
          <Logo/>
          <div>
            <Button as={Link} to="/start" colorScheme="purple">Start</Button>
          </div>
          {buildInfo && (
              <div>
                <Text fontSize="xs" mt={20}>Built at {buildInfo}</Text>
              </div>
          )}
        </VStack>
      </Container>
  )
}

export default Home