import React from 'react'
import ActiveProjects from './views/ActiveProjects'
import {FaBuilding, FaCalendarDay, FaHistory, FaPlay, FaUsers} from 'react-icons/fa'
import UpcomingProjects from './views/UpcomingProjects'
import EndedProjects from './views/EndedProjects'
import {Role} from './domain/Person'
import OrganizationProfile from './views/OrganizationProfile'
import Members from './views/Members'

export interface Route {
  path: string
  title: string
  icon?: React.ReactNode,
  component?: React.FC,
  children?: Route[]
  scope?: Role[]
}

const routes: Route[] = [
  {
    path: '/my-projects',
    title: 'My projects',
    children: [
      {
        path: '/active-projects',
        title: 'Active projects',
        component: ActiveProjects,
        icon: <FaPlay />,
      },
      {
        path: '/upcoming-projects',
        title: 'Upcoming projects',
        component: UpcomingProjects,
        icon: <FaCalendarDay />,
      },
      {
        path: '/ended-projects',
        title: 'History',
        component: EndedProjects,
        icon: <FaHistory />,
      }
    ]
  },
  {
    path: '/profile',
    title: 'Profile',
    children: [
      {
        path: '/organization-profile',
        title: 'Organization profile',
        component: OrganizationProfile,
        icon: <FaBuilding />,
        scope: [Role.ADMIN]
      },
      {
        path: '/organization-members',
        title: 'Organization members',
        component: Members,
        icon: <FaUsers />,
        scope: [Role.MANAGER, Role.ADMIN]
      }
    ]
  }
]

export default routes