import React from 'react'
import Project from '../domain/Project'
import {SimpleGrid, Text, VStack, ScaleFade, IconButton, HStack} from '@chakra-ui/react'
import TagsGrid from './TagsGrid'
import ProjectBadge from './ProjectBadge'
import {formatDate} from '../util/date'
import Card from '../ui/Card'
import Badge from '../ui/Badge'
import {Link} from 'react-router-dom'
import {FaPen} from 'react-icons/fa'

export interface ProjectCardProps {
  project: Project
  highlighted?: boolean
}

const ProjectCard: React.FC<ProjectCardProps> = ({project, highlighted}) => {
  const dateString = formatDate(project.hasStarted() ? project.dateEnd : project.dateStart)

  const content = (
      <Card>
        <SimpleGrid columns={4} spacing={10} templateColumns="1fr auto auto auto" alignItems="center">
          <Text fontWeight="bold">
            {project.name}
            {project.clientName && <Badge ml={2} colorScheme="purple" size="xs">{project.clientName}</Badge>}
          </Text>
          <TagsGrid tags={[
            {
              name: 'Team of 3',
            },
            {
              name: 'Flex'
            }
          ]}/>
          <VStack>
            <ProjectBadge project={project}/>
            {dateString && <Text fontSize="xs">{dateString}</Text>}
          </VStack>
          <IconButton as={Link} to={`?project=${project.id}`} aria-label="Edit" icon={<FaPen/>}/>
        </SimpleGrid>
      </Card>
  )

  if (!highlighted) {
    return content
  }

  return (
      <ScaleFade in initialScale={0.3}>
        {content}
      </ScaleFade>
  )
}

export default ProjectCard