import React from 'react'
import {Input, Select, Stack} from '@chakra-ui/react'
import FormField from './FormField'
import FormImageField from './FormImageField'
import {Role} from '../domain/Person'
import useStore from '../hooks/useStore'
import {observer} from 'mobx-react'
import DrawerForm from './DrawerForm'
import useQueryValue from '../hooks/useQueryValue'

interface PersonFormDrawerProps {
}

const PersonFormDrawer: React.FC<PersonFormDrawerProps> = () => {
  const name = 'person'
  const {personStore, organizationStore} = useStore()
  const personId = useQueryValue(name)

  const handleSubmit = async (values: any, personId: string) => {
    if (personId) {
      await personStore.update({
        id: personId,
        ...values
      })
    } else {
      await personStore.create({
        ...values,
        organizationId: organizationStore.currentOrganizationId
      })
    }
  }

  const person = {
    role: Role.USER,
    ...personId ? personStore.getById(personId) : {}
  }

  return (
      <DrawerForm
          name={name}
          onSubmit={handleSubmit}
          header={<>{personId ? 'Edit' : 'Add'} member</>}
          defaultValues={person}
      >
        <Stack spacing={4}>
          <FormField name="name" label="Name" required>
            <Input autoFocus={!personId} />
          </FormField>

          <FormField name="user.email" label="Email">
            <Input type="email" />
          </FormField>

          <FormField name="position" label="Position">
            <Input />
          </FormField>

          <FormField name="role" label="Role">
            <Select>
              <option value={Role.ADMIN}>Admin</option>
              <option value={Role.MANAGER}>Manager</option>
              <option value={Role.USER}>User</option>
            </Select>
          </FormField>

          <FormImageField label="Image" name="image" />
        </Stack>
      </DrawerForm>
  )
}

export default observer(PersonFormDrawer)