import React from 'react'
import {Avatar, HStack, Text, StackProps} from '@chakra-ui/react'

export interface UserProps extends StackProps {
  name: string
  subTitle: string
  image?: string
  align?: 'left' | 'right'
}

const User: React.FC<UserProps> = ({ name, subTitle, image, align, ...stackProps}) => {
  const avatar = <Avatar name={name} src={image} size="sm" />
  return (
      <HStack spacing={2} {...stackProps}>
        {align === 'left' && avatar}
        <Text align={align}>
          <Text fontSize="sm">{name}</Text>
          <Text fontSize="xs" color="gray.500">{subTitle}</Text>
        </Text>
        {align === 'right' && avatar}
      </HStack>
  )
}

User.defaultProps = {
  align: 'left'
}

export default User