import React from 'react'
import {Input, InputProps} from '@chakra-ui/react'
import {format} from 'date-fns'
import {toDate} from '../util/date'

export interface DateInputProps extends Omit<InputProps, 'type' | 'value'> {
  value?: Date
}

const DateInput: React.FC<DateInputProps> = ({ value, ...props}) => {
  const dateValue = value && toDate(value)

  return (
      <Input {...props} value={dateValue && format(dateValue, 'yyyy-MM-dd')} type="date" />
  )
}

export default DateInput