import React from 'react'
import Badge from '../ui/Badge'
import Person, {Role} from '../domain/Person'

export interface RoleBadgeProps {
  person: Person
}

const RoleBadge: React.FC<RoleBadgeProps> = ({person}) => {
  let colorScheme: string

  switch (person.role) {
    case Role.ADMIN:
      colorScheme = 'purple'
      break

    case Role.USER:
      colorScheme = 'yellow'
      break

    case Role.MANAGER:
      colorScheme = 'green'
      break
  }

  return (
      <Badge colorScheme={colorScheme}>{person.humanReadableRole}</Badge>
  )
}

export default RoleBadge