import React from 'react'
import {FormControl, FormErrorMessage, FormHelperText, FormLabel,} from '@chakra-ui/react'
import {Controller, useFormContext} from 'react-hook-form'

interface FormFieldProps {
  name: string
  required?: boolean
  label?: string
  helperText?: string
  defaultValue?: string
  pattern?: RegExp
  validate?: (value: any, getValues: () => any) => string | true
}

const FormField: React.FC<FormFieldProps> = ({children, name, required, label, helperText, defaultValue, pattern, validate}) => {
  const {errors, control, getValues} = useFormContext()
  const error = errors[name]?.message || null

  if (React.Children.count(children) > 1) {
    throw new Error('Invalid children count')
  }

  const child = children && React.Children.only(children)

  return (
      <Controller
        control={control}
        name={name}
        rules={{
          required,
          pattern,
          validate: validate && ((value: any) => validate(value, getValues)),
        }}
        defaultValue={defaultValue}
        render={({ onChange, onBlur, value }) => (
            <FormControl
                id={`formField__${name}`}
                isRequired={required}
                isInvalid={!!error}
            >
              {label && <FormLabel>{label}</FormLabel>}
              {child && React.isValidElement(child) && React.cloneElement(child, {
                name,
                onChange,
                onBlur,
                value
              })}
              {error && <FormErrorMessage>{error}</FormErrorMessage>}
              {helperText && <FormHelperText>{helperText}</FormHelperText>}

            </FormControl>
        )}
      />
  )
}

FormField.defaultProps = {
  defaultValue: '',
}

export default FormField