import React from 'react'
import {useHistory, useLocation} from 'react-router-dom'
import {Drawer} from '@chakra-ui/react'
import useQueryValue from '../hooks/useQueryValue'

export interface DrawerContext {
  value: string
  handleClose: () => void
}

export interface RouterDrawerProps {
  name: string
  children?: ((drawerContext: DrawerContext) => any) | React.ReactNode
}

const RouterDrawer: React.FC<RouterDrawerProps> = ({name, children}) => {
  const location = useLocation()
  const history = useHistory()
  const value = useQueryValue(name)
  const isOpen = value !== null

  const handleClose = () => {
    const urlSearchParams = new URLSearchParams(location.search)
    urlSearchParams.delete(name)

    history.push(`?${urlSearchParams.toString()}`)
  }

  return (
      <Drawer isOpen={isOpen} onClose={handleClose} placement="right">
        {typeof children === 'function' ? (children as any)({value, handleClose}) : children}
      </Drawer>
  )
}

export default RouterDrawer