import React from 'react'
import {
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  Input,
  Stack
} from '@chakra-ui/react'
import RouterDrawer from './RouterDrawer'
import Form from './Form'
import FormField from './FormField'
import FormSubmitButton from './FormSubmitButton'
import {useHistory} from 'react-router-dom'
import {Role} from '../domain/Person'
import useStore from '../hooks/useStore'

interface PersonDeleteDrawerProps {
}

const PersonDeleteDrawer: React.FC<PersonDeleteDrawerProps> = () => {
  const history = useHistory()
  const {personStore} = useStore()

  const handleSubmit = (personId: string) => async () => {
    await personStore.delete(personId)
    history.push('?')
  }

  return (
      <RouterDrawer name="person.delete">
        {(personId: string) => {
          const person = {
            role: Role.USER,
            ...personId ? personStore.getById(personId) : {}
          }

          return (
              <Form onSubmit={handleSubmit(personId)}>
                <DrawerOverlay>
                  <DrawerContent>
                    <DrawerCloseButton/>
                    <DrawerHeader>Delete member</DrawerHeader>

                    <DrawerBody>
                      <Stack spacing={4}>
                        <FormField
                            name="confirmation"
                            label={`Type "${person.name}" to delete`}
                            required
                            validate={(value: string) => value === person.name || 'Invalid'}
                        >
                          <Input autoFocus />
                        </FormField>
                      </Stack>
                    </DrawerBody>

                    <DrawerFooter>
                      <FormSubmitButton>Delete</FormSubmitButton>
                    </DrawerFooter>
                  </DrawerContent>
                </DrawerOverlay>
              </Form>
          )
        }}
      </RouterDrawer>
  )
}

export default PersonDeleteDrawer