import React from 'react'
import {Input, Stack} from '@chakra-ui/react'
import FormField from './FormField'
import Organization from '../domain/Organization'
import FormImageField from './FormImageField'
import DrawerForm from './DrawerForm'

export interface OrganizationEditFormProps {
  onSubmit: (data: any) => void
  organization: Organization
}

const OrganizationFormDrawer: React.FC<OrganizationEditFormProps> = ({onSubmit, organization}) => {
  return (
      <DrawerForm
          name="organization"
          onSubmit={onSubmit}
          defaultValues={organization}
          successMessage="Organization saved successfully!"
      >
        <Stack spacing={4}>
          <FormField name="name" required label="Organization name">
            <Input/>
          </FormField>
          <FormImageField label="Logo" name="logo"/>
        </Stack>
      </DrawerForm>
  )
}

export default OrganizationFormDrawer